import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Divider from "@mui/material/Divider";

const dispatchedEvents = [];

const SrsMuiDivider = function (props, ref) {
  return <Divider {...props} theme={props.muiTheme}/>;
};

const styles = `
  /* Custom styles for the MuiDivider component */
`;

const props = [
  {
    name: "orientation",
    defaultValue: "horizontal",
    type: "string",
  },
  {
    name: "variant",
    defaultValue: "fullWidth",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Divider if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiDivider),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
